import React from 'react';
import { default as MuiAccordion, AccordionProps } from '@mui/material/Accordion';

// Modified reason: modifying the default props.
const Accordion = React.forwardRef<HTMLElement, AccordionProps>( ( props, ref ) => {
	return (
		<MuiAccordion
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<AccordionProps>;

Accordion.defaultProps = {
	variant: 'outlined',
};

export default Accordion;
